body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: beige;
}

button {
  margin-left: 5px;
  padding: 14px;
  background-color: #ff00e1;
  color: white;
  border: none;
  border-radius: 13px;
  cursor: pointer;
}

button:hover {
  background-color: #b30095;
}
